import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import {BoilerStateList} from './boilerStates'
import * as serviceWorker from './serviceWorker';

const appEl = document.getElementById('root')
if (appEl){
  ReactDOM.render(<React.StrictMode><App/></React.StrictMode>, appEl);
}

const boilerStateEl = document.getElementById('boilerStatesDiv')
if (boilerStateEl){
  ReactDOM.render(<React.StrictMode><BoilerStateList/></React.StrictMode>, boilerStateEl);
}


