export function loadBoilerStates(callback){
    const xhr = new XMLHttpRequest()
    const method = 'GET'
    const url = "http://127.0.0.1:8000/api/boilerStates/0"
    const responseType = "json"
  
    xhr.responseType = responseType
    xhr.open(method, url)
    xhr.onload = function() {
        callback(xhr.response, xhr.status)
        // const contentEl = document.getElementById("card-content")
        // const serverResponse = xhr.response
        // console.log(xhr.response)
        // if (this.status === 200){
        //     thermostatSetTemp = serverResponse.traits["sdm.devices.traits.ThermostatTemperatureSetpoint"]["heatCelsius"]
        //     ambientTemp = serverResponse.traits["sdm.devices.traits.Temperature"]["ambientTemperatureCelsius"]
        //     contentEl.innerHTML = "<h5>Ambient Temp: "+ ambientTemp.toFixed(1) +"</h5>"
        //     contentEl.innerHTML += "<h5>Thermostat Set Temp: "+ thermostatSetTemp.toFixed(1)  +"</h5>"
        // }
        // else{
        //     contentEl.innerHTML = "ERROR: " + serverResponse.message
        // }
    }
    xhr.onerror = function (e) {
      console.log(e)
      callback({"message": "The request was an error"}, 400)
    }
    xhr.send()
  
}
  